import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/login";

class ResidentialService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };

  findByUid = (loanUid: string, applicantUid: string) => {
    const url = `${DEFAULT_PATH}/basic/info/residential/address/${loanUid}/${applicantUid}`;
    return this.makeGetRequestWithAuth(url);
  };

  save = (obj: any) => {
    const url = `${DEFAULT_PATH}/qde/basic/info/residential/address`;
    return this.makePostRequestWithAuth(url, obj);
  };

  saveForDde = (obj: any) => {
    const url = `${DEFAULT_PATH}/dde/basic/info/residential/address`;
    return this.makePutRequestWithAuth(url, obj);
  };

  update = (obj: any) => {
    const url = `${DEFAULT_PATH}/dde/basic/info/residential/address`;
    return this.makePostRequestWithAuth(url, obj);
  };

  createResidentialAddress = (obj: any) => {
    const url = `${DEFAULT_PATH}/dde/basic/info/residential/address`;
    return this.makePostRequestWithAuth(url, obj);
  };

  // ------------------------- To get the applicant residential address for DDE ------------------------- //

  getAddressForDde = (loanId: string, applicantUid: string) => {
    const url = `${DEFAULT_PATH}/basic/info/residential/address/${loanId}/${applicantUid}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new ResidentialService();
